<template>
    <div class="container">
        <Aside 
          :status="asideStatus" 
          :navData="navInfo" />
       <div class="bd-container">
            <!-- :navList="navList" -->
          <Header 
            @setAsideStatus="setAsideStatus"
            @getNavInfo="getNavInfo"
          />
        <div class="bd-content scroll-container">
          <div class="content">
            <router-view />
          </div>
        </div>
       </div>
    </div>
</template>

<script>
import Header from '../components/header.vue'
import Aside from '../components/aside.vue'

export default {
  components:{
    Header,
    Aside,
  },
  data(){
    return {
      isContent: false,
      isList: true,
      content: '',
      mdTile: '',
      articles: [],
      asideStatus: '',
      navInfo: {},
      navList: []
    }
  },
  methods: {
    setAsideStatus(val) {
      if (val) {
        if (!this.asideStatus) return
        this.asideStatus = val;
        return
      }
      if (!this.asideStatus || this.asideStatus === 'open') {
        this.asideStatus='hide'
      } else {
        this.asideStatus = 'open'
      }
    },
    getNavInfo(nav) {
      if (nav) this.navInfo = nav
    },
    clipPage(content){
        this.content = content;
        this.isContent = false;
        this.isList = false;
    },
    goIndex(){
        this.isContent = false;
        this.isList = true;
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
}
.bd-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-x: overlay;
  overflow-y: hidden;
}
.bd-content{
  flex: 1;
  display: flex;
  justify-content: center;
  background: #fff;
}
.content {
  width: 700px;
}
</style>
