<template>
  <div>
    <template v-if="curModal.index"><Index /></template>
    <template v-else-if="curModal.webpage"><wep-page :title="title"/></template>
    <template v-else-if="curModal.other"><other :title="title"/></template>
    <template v-else>页面加载错误。。。</template>
  </div>
</template>

<script>
import { getFileNameByPath, isRealArr } from "../utils/index";
import Index from '../pages/modal/index.vue'
import WepPage from '../pages/modal/web-page.vue'
import Other from '../pages/modal/other.vue'
import storage from '../utils/storage'

export default {
  components: {
    Index,
    WepPage,
    Other
  },
  name: getFileNameByPath(),
  data() {
    return {
      title: '',
      modalPath: '',
      curModal: {
        index: false,
        webpage: false,
        other: false
      }
    }
  },
  created() {
    this.init()
  },
  watch: {
    '$route.params': {
      handler() {
        this.init()
      }
    }
  },
  methods: {
    init() {
      const path = getFileNameByPath()
      this.modalPath = path
      this.isCurPage()
      if (this.getPageType()==='nav') this.initNavPage()
    },
    getPageType() {
      return location.hash.split('/', 2).pop();
    },
    isCurPage() {
      const webpage = ['base', 'frame', 'engine', 'cli']
      if (this.getPageType()==='nav') {
        this.curModal = {
          ...this.curModal,
          index: this.modalPath === 'index',
          webpage: webpage.some(item => item === this.modalPath),
          other: this.modalPath === 'other'
        }
      }
    },
    fomateNavListToObj() {
      const { navList } = storage.getData('navList') || {}
      if (!isRealArr(navList)) return
      const results = {}
      navList.forEach(nav => {
        const {key, value} = nav;
        results[key] = value
      })
      return results
    },
    initNavPage() {
      const navObj = this.fomateNavListToObj()
      this.title = navObj[this.modalPath]
    }
  }
}
</script>

