import { createRouter, createWebHashHistory} from "vue-router";
import basePage from '../components/base-page.vue'
import articalPage from '../components/artical-page.vue'



const routes = [
  {
    path: '/',
    redirect: '/nav/index',
  },
  {
    path: '/nav/:navName',
    name: 'basePage',
    component: basePage
  },
  {
    path: '/artical/:articalName+',
    name: 'articalPage',
    component: articalPage
  },
]

const router = new createRouter({
  history: createWebHashHistory(), // 使用history刷新404，没有ngix，只能走hash
  routes
})

export default router