export const isStr = val => val && typeof val === 'string'

export const isNum = val => typeof val === 'number' && val === val

export const isObj = obj => obj && Object.prototype.toString.call(obj) === "[object Object]"

export const isRealObj = obj => isObj(obj) && Reflect.ownKeys(obj).length

export const isArr = arr => arr && Array.isArray(arr)

export const isRealArr = arr => isArr(arr) && arr.length

const  metaType = obj => {
  const MAP = {
    '[object String]': 'string',
    '[object Number]': 'number',
    '[object Boolean]': 'boolean',
    '[object Null]': 'null',
    '[object Undefined]': 'undefined',
    '[object Symbol]': 'symbol',
    '[object BigInt]': 'bigInt',
    '[object Object]': 'object',
    '[object Function]': 'function',
    '[object Array]': 'array',
    '[object Date]': 'date',
    '[object RegExp]': 'regExp',
    '[object Error]': 'error'
  };
  return MAP[Object.prototype.toString.call(obj)];
}

export const deepCopy = data => {
  const type = metaType(data);
  let obj = null;
  if (type === 'array') {
      obj = [];
      for (let i = 0; i < data.length; i++) {
          obj.push(deepCopy(data[i]));
      }
  } else if (type === 'object') {
      obj = {};
      for (const key in data) {
          if (Reflect.ownKeys(data).length) {
              obj[key] = deepCopy(data[key]);
          }
      }
  } else {
      return data;
  }
  return obj;
}

export const getFileNameByPath = () => location.hash.split('/').pop()

export const getObjIndexInArr = (arr, key) => {
  let flag = -1;
  arr.some((item, index) => {
    if (item.key === key) {
      flag = index
      return true
    }
    return false;
  })
  return flag
}

/**
 * 初始化nav的active,用于页面刷新
 */
export const initNavItemActive = (navList) => {
  const levels = location.hash.split('/');
  levels.splice(0, 2)
  const modifyNavActive = (levels, navList) => {
    const level = levels.shift()
    const index = getObjIndexInArr(navList, level)
    if (index >= 0) {
      navList[index] = { ...navList[index], isActive: true };
      if (isRealArr(navList[index].children)) modifyNavActive(levels, navList[index].children)
    }
  }

  if (isRealArr(levels)) modifyNavActive(levels, navList)
  console.log(111, navList)
  return navList
}