<template>
  <div v-html="articles && articles.content"></div>
</template>

<script>
import { getFileNameByPath } from "../utils/index";
import fetch from '../utils/fetch'

export default {
 data() {
    return {
      articles: {},
    }
  },
  created() {
    this.init()
  },
  watch: {
    '$route.params': {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  methods: {
    init() {
      if (this.getPageType()==='artical') this.initArticalPage()
    },
    getPageType() {
      return location.hash.split('/', 2).pop();
    },
    initArticalPage() {
      const path = getFileNameByPath()
      fetch(`static/parsefile/${path}.json`).then(res => {
        this.articles = res.data;
      })
    },
  }
}
</script>

